@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.scroll{position:relative;margin-top:66px;box-shadow:inset 0px -1px 0px #e0e0e0,inset 0px 1px 0px #e0e0e0;white-space:nowrap;overflow-x:auto;-webkit-overflow-scrolling:touch;-ms-overflow-style:-ms-autohiding-scrollbar}.scroll::-webkit-scrollbar{background-color:#d3d3d3;height:6px}.scroll::-webkit-scrollbar-thumb{background-color:#a9a9a9}.scroll::before,.scroll::after{content:"";position:fixed;top:66px;height:53px;width:42px;pointer-events:none;background:linear-gradient(90deg, rgba(196, 196, 196, 0.3) 0%, rgba(242, 242, 242, 0) 88.1%)}.scroll::before{left:0}.scroll::after{right:0;transform:rotate(-180deg)}.scroll.no-left::before{display:none}.scroll.no-right::after{display:none}.nav-list{display:flex;list-style:none;margin:0;padding:8px 33px}.nav-item button{font-family:"Lato",sans-serif;padding:8px 24px;outline:none;border:none;background-color:transparent;display:flex;align-items:center;justify-content:center;border-radius:2px;font-weight:400;font-size:14px;line-height:1.4;color:#666}.nav-item button:hover{background:#e0e0e0;color:#666}.nav-item button.active{background-color:#cfe5ff;font-weight:700;color:#527aaa;padding-left:23px;padding-right:23px}.nav-item button:disabled{color:#afafaf;cursor:not-allowed}
